:root {
    --icon_width: 35px
}

.form .field-container {
    width: 100% !important;
    position: relative;
    margin-bottom: 25px
}

.form .field-container .icon_container {
    position: relative
}

.form .field-container .icon_container i {
    width: var(--icon_width);
    height: var(--icon_width);
    padding: 8px 0;
    background-color: #9a1e4f;
    position: absolute;
    left: 0;
    top: 0;
    color: #fff;
    text-align: center
}

.field-container input:not([type=checkbox]):not([type=radio]) {
    width: 100%;
    height: var(--icon_width);
    padding: 8px 8px 8px 40px
}

.field-container textarea {
    width: 100%;
    padding: 8px;
    height: auto
}

.field-container label[for*=gender] {
    width: 25%;
    display: flex;
    justify-content: space-between;
    align-items: center
}

.form .field-msg,
.form .field-msg_wrong {
    display: none;
    position: absolute;
    left: 0;
    bottom: -24px;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: .05em;
    border: solid 2px 0 0 0 transparent;
    width: 100%;
    padding: 0 3px
}

.form .field-msg.show,
.form .field-msg_wrong.show {
    display: block;
    border-top: solid 2px red
}

.form label>.field-msg_wrong.show {
    display: inline-block;
    border-top: solid 2px transparent
}

.js-form-error,
.js-form-submission,
.js-form-success {
    border: solid 2px transparent !important
}

.form .field-msg.error,
.form .field-msg_wrong {
    color: red
}

.form .success {
    color: green;
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 400;
    bottom: -45px;
    
}



:required,
input:not([type=checkbox]):required {
    background-image: url(astrix.png);
    background-size: 8px;
    background-position: right 5px top 5px;
    background-repeat: no-repeat
}
